body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* background: #cfcdc7; */

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* button - start */
.MuiButton-contained {
  min-width: 8% !important;
  font-weight: bold !important;
}
/* button - end */
.pl,
.fl {
  float: left;
}

.pr,
.fr {
  float: right;
}

.clear {
  clear: both !important;
}

.ml5 {
  margin-left: 5px;
}

.mt5 {
  margin-top: 5px;
}

.mr5 {
  margin-right: 5px !important;
}

.mb5 {
  margin-bottom: 5px;
}

.pl0 {
  padding-left: 0 !important;
}
.pl5 {
  padding-left: 5px;
}

.pt5 {
  padding-top: 5px;
}

.pr5 {
  padding-right: 5px;
}

.pb5 {
  padding-bottom: 5px;
}

.main-cont {
  padding: 2em;
}

.page-cont {
  width: 100%;
}

.form-cont {
  width: 100%;
}

.p-rel {
  position: relative;
}

.p-abs {
  position: absolute;
}

.bold {
  font-weight: bold;
}
.img-full {
  width: 100%;
}
.addBtn {
  width: 7em;
  padding: 10px 10px;
  font-size: 20px;
  font-weight: bold;
  border: none;
  background: #02ef09;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

.closeBtn {
  position: absolute !important;
  top: 2%;
  right: 1%;
}

