.title1 {
  text-transform: uppercase;
  color: #242222;
  font-family: sans-serif;
}

.title2 {
  color: #242222;
  font-family: sans-serif;
  text-transform: uppercase;
}

.customInputBorder {
  padding: 11px 20px;
  border-radius: 5px;
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.23);
}

.customInputBorder .customInputBorderLabel {
  padding: 0 3px;
  background: #fff;
  color: rgb(0 0 0 / 59%);
  top: -10px;
  position: absolute;
  left: 10px;
  font-weight: 400;
  font-size: 0.75em;
}
.temp-img{
  width: 65%;
}
.card-container {
  padding-top: 15px;
  margin-bottom: 15px;
}

.print-btn {
  top: 4em !important;
    position: absolute !important;
    right: 1em !important;
}

.linkedin-icon-size{
  vertical-align: middle !important;
  width: 28% !important;
}

.position-btn{
  border-radius: 5px !important;
  cursor: pointer;
  background: #eee !important;
}
